// ‚úÖ Import React (Fix: Required for JSX)
import React from "react";
import { Routes, Route } from "react-router-dom";
// Import LoginPanel component for user login
import LoginPanel from "./components/Login/Login";
// Import RegisterPanel component for user registration
import RegisterPanel from "./components/Register/Register"; 
// Import Home component which serves as the landing page
import Home from "./components/Home/Home"; 
// Import Dealers component to list all dealers
import Dealers from "./components/Dealers/Dealers";   // ‚úÖ New Import
// Import Dealer component to show details and reviews for a specific dealer
import Dealer from "./components/Dealers/Dealer";     // ‚úÖ New Import
// Import PostReview component to allow logged-in users to submit reviews
import PostReview from "./components/Dealers/PostReview";  // ‚úÖ New Import

function App() {
  return (
    <Routes>
      {/* ‚úÖ Home Page - Detects if user is logged in */}
      <Route path="/" element={<Home />} />

      {/* ‚úÖ Login Page */}
      <Route path="/login" element={<LoginPanel />} />

      {/* ‚úÖ Registration Page */}
      <Route path="/register" element={<RegisterPanel />} />

      {/* ‚úÖ Dealers Page - Lists all dealers */}
      <Route path="/dealers" element={<Dealers />} />

      {/* ‚úÖ Dealer Page - Shows details and reviews for a dealer */}
      <Route path="/dealer/:id" element={<Dealer />} />

      {/* ‚úÖ Post Review Page - Allows logged-in users to submit reviews */}
      <Route path="/postreview/:id" element={<PostReview />} />
      {/* üîß SUGGESTION: Ensure your React Router version is compatible with your code setup */}
    </Routes>
  );
}

export default App;